.App {
    text-align: center;
    align-items: center;
}

body {
    background-color: #b3e5fc;
    border-radius: 10px;
    font-family: "Raleway", sans-serif;
    text-align: center;
}

h1 {
    text-align: center;
    margin: 20px 0;
}

.textLeft {
    text-align: left;
}

.card {
    width: 450px;
    border: none;
    border-radius: 10px;
    background-color: #fff;
    margin: auto;
}

.image {
    min-width: 155px;
    margin-right: 20px;
}

.stats {
    background: #f2f5f8 !important;
    color: #000 !important;
}

.articles {
    font-size: 10px;
    color: #a1aab9;
}

.number1 {
    font-weight: 500;
}

.followers {
    font-size: 10px;
    color: #a1aab9;
}

.number2 {
    font-weight: 500;
}

.rating {
    font-size: 10px;
    color: #a1aab9;
}

.number3 {
    font-weight: 500;
}